import './App.css'
import Main from './page/main/Main'
import { Qna, Notice } from './page/board'
import { Login, MyPage, SignIn } from './page/member'
import { Routes, Route } from 'react-router-dom'
import { Container, Row, Col, Navbar, Nav } from 'react-bootstrap'
import { useEffect, useState } from 'react'
import RegisterPage from './page/member/RegisterPage'
import { useAuthStore } from './store/useAuthStore'

function App() {
  const [expanded, setExpanded] = useState(false)

  const {token,initialize, logout } = useAuthStore();
  useEffect(() => {
      initialize();
  }, [])

  const handleLogout = () => {
  logout();
    window.location.href = '/'
  }


  return (
    <Container fluid className="App p-0">
      {/* Header */}
      <Navbar expand="md" bg="light" className="py-3" expanded={expanded} onToggle={setExpanded}>
        <Container>
          <Navbar.Brand href="/"></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <Nav.Link href="/download"> </Nav.Link>
              <Nav.Link href="/notice/list"></Nav.Link>
              <Nav.Link href="/qna/list">QNA</Nav.Link>
              <Nav.Link href="/guide"></Nav.Link>

              {token ? (
                <>
                  <Nav.Link href="/mypage"></Nav.Link>
                  <Nav.Link onClick={handleLogout}></Nav.Link>
                </>
              ) : (
                <>
                  <Nav.Link href="/login"></Nav.Link>
                  <Nav.Link href="/RegisterPage"></Nav.Link>
                </>
              )}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      {/* Body */}
      <main className="flex-grow-1">
        <Routes>
          <Route path="/" element={<Main />} />
          <Route path="/login" element={<Login />} />
          <Route path="/mypage" element={<MyPage />} />
          <Route path="/notice/*" element={<Notice />} />
          <Route path="/qna/*" element={<Qna />} />
          <Route path="/RegisterPage/*" element={<RegisterPage />} />

        </Routes>
      </main>

      {/* Footer */}
      <footer className="py-4 mt-5 footer bg-dark text-light">
        <Container>
          <Row>
            <Col md={6} className="mb-3 text-center text-md-start mb-md-0">
              <div></div>
              <div> CompanyName. All rights reserved.</div>
            </Col>
            <Col md={6} className="text-center text-md-end">
              <Nav className="justify-content-center justify-content-md-end">
                <Nav.Link className="text-light" href="/guide">
                  
                </Nav.Link>
                <Nav.Link className="text-light" href="/download">
                   
                </Nav.Link>
                <Nav.Link className="text-light" href="/notice/list">
                  
                </Nav.Link>
                <Nav.Link className="text-light" href="/qna/list">
                  QNA
                </Nav.Link>
              </Nav>
            </Col>
          </Row>
        </Container>
      </footer>
    </Container>
  )
}
// test
export default App
