import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './css/RegisterPage.css';

const RegisterPage = () => {
  const navi = useNavigate();

  const [form, setForm] = useState({
    loginId: '',
    password: '',
    rePassword: '',
    nickname: '',
    email: '',
    postcode: '',
    address: '',
    detailAddress: ''
  });

  const [pwMessage, setPwMessage] = useState('');
  const [pwMatchMessage, setPwMatchMessage] = useState('');
  const [emailMessage, setEmailMessage] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [modalContent, setModalContent] = useState('');

  const idInputRef = useRef(null);
  const nickNameInputRef = useRef(null);

  const openModal = (content) => {
    setModalContent(content);
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
    setModalContent('');
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));

    if (name === 'password') {
      const passwordPattern = /^(?=.*[a-zA-Z])(?=.*\d).{8,}$/;
      setPwMessage(
        !passwordPattern.test(value)
          ? ' 8 ,    .'
          : ''
      );
    }

    if (name === 'rePassword' || (name === 'password' && form.rePassword)) {
      const pwToCheck = name === 'rePassword' ? value : form.rePassword;
      setPwMatchMessage(
        form.password !== pwToCheck
          ? '  .'
          : ' .'
      );
    }

    if (name === 'email') {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      setEmailMessage(
        emailPattern.test(value)
          ? '  .'
          : '  .'
      );
    }
  };

  const checkId = async () => {
    if (!form.loginId) {
      alert(" .");
      idInputRef.current.focus();
      return;
    }

    try {
      const response = await axios.get(`http://localhost/api/member/checkId/${form.loginId}`);
      const exists = response.data;

      if (exists) {
        alert("   .");
        setForm({ ...form, loginId: '' });
        idInputRef.current.focus();
      } else {
        alert("  .");
      }
    } catch (err) {
      alert(" ");
      console.error(err);
    }
  };

  const checkNickName = async () => {
    if (!form.nickname) {
      alert(" .");
      nickNameInputRef.current.focus();
      return;
    }

    try {
      const response = await axios.get(`http://localhost/api/member/checkNickName/${form.nickname}`);
      const exists = response.data;

      if (exists) {
        alert("   .");
        setForm({ ...form, nickname: '' });
        nickNameInputRef.current.focus();
      } else {
        alert("  .");
      }
    } catch (err) {
      alert(" ");
      console.error(err);
    }
  };

  const handlePostcode = () => {
    new window.daum.Postcode({
      oncomplete: function (data) {
        let fullAddress = data.address;
        let extraAddress = '';

        if (data.addressType === 'R') {
          if (data.bname !== '') extraAddress += data.bname;
          if (data.buildingName !== '') {
            extraAddress += (extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName);
          }
          if (extraAddress !== '') {
            fullAddress += ` (${extraAddress})`;
          }
        }

        setForm((prev) => ({
          ...prev,
          postcode: data.zonecode,
          address: fullAddress
        }));
      }
    }).open();
  };
  const [agreedType, setAgreedType] = useState('');
  const [agreements, setAgreements] = useState({
    terms: false,
    privacy: false,
    marketing: false
  });
  const confirmAndCheck = () => {
    if (agreedType) {
      setAgreements(prev => ({ ...prev, [agreedType]: true }));
    }
    closeModal();
  };
    
  const handleSubmit = async (e) => {
    e.preventDefault();
    const { loginId, password, rePassword, nickname, email, postcode, address, detailAddress } = form;

    if (!loginId || !password || !rePassword || !nickname || !email || !postcode || !address || !detailAddress) {
      alert("   .");
      return;
    }

    try {
      await axios.post('http://localhost:80/api/member/register', form);
      alert(' ');
      navi("/");
    } catch (err) {
      alert(' ');
      console.error(err);
    }
  };

  return (
    <div className="container">
      <h2 className="text-xl font-bold mb-4"></h2>
      <form onSubmit={handleSubmit}>
        <div className='box'>
         <input name="loginId" ref={idInputRef} placeholder="" onChange={handleChange} className="input" />
        <button className="btn" type="button" onClick={checkId}></button>
        </div><br />
   <div className='box'>
         <input name="password" type="password" placeholder="" onChange={handleChange} className="input" /><br />
        <div className="text-red-500 text-sm">{pwMessage}</div>
</div>
   <div className='box'>
          <input name="rePassword" type="password" placeholder="" onChange={handleChange} className="input" />
        <div className="text-sm" style={{ color: pwMatchMessage.includes('') ? 'red' : 'green' }}>
          {pwMatchMessage}
        </div>
                </div>
         <div className='box'>
         <input name="nickname" ref={nickNameInputRef} placeholder="" onChange={handleChange} className="input" />
        <button className="btn" type='button' onClick={checkNickName}></button>
        </div><br />
         <div className='box'>
         <input type="email" name="email" onChange={handleChange} className="input" /></div><br />
        <div className="text-sm" style={{ color: emailMessage.includes('') ? 'green' : 'red' }}>
          {emailMessage}
        </div>

        <div className="form-group">
          <label></label>
          <div className="input-with-btn">
            <input type="text" className="input" name="postcode" value={form.postcode} readOnly />
            <button type="button" className="btn"onClick={handlePostcode}></button>
          </div>
        </div>

        <div className="form-group">
          <label></label>
          <input type="text" className="input" name="address" value={form.address} readOnly />
        </div>

        <div className="form-group">
          <label></label>
          <input type="text" className="input" name="detailAddress" value={form.detailAddress} onChange={handleChange} />
        </div>

        <div className="agreement-section mt-4">
          <label>
            <input type="checkbox" required />  
            <button type="button" onClick={() => openModal(' .')}></button>
          </label><br />
          <label>
            <input type="checkbox" required />   
            <button type="button" onClick={() => openModal(`
[    ]

1.  :
- : , , , , , 
- :     

2.  :
-     
-     
-     
-    (   )

3.    :
-   
- ,          

4.     :
-     ,        .

    ,     .
            `)}></button>
          </label><br />
       
        </div>

        <button type="submit"className="btn" ></button>
      </form>

      {showModal && (
        <div className="modal-backdrop" onClick={closeModal}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h3> </h3>
            <pre style={{ whiteSpace: 'pre-wrap' }}>{modalContent}</pre>
            <div className="modal-buttons">
              <button onClick={closeModal}></button>
              <button onClick={confirmAndCheck}></button>
            </div>


          </div>
        </div>
      )}
    </div>
  );
};

export default RegisterPage;
