import React, { useEffect, useState } from 'react';
import caxios from '../../lib/caxios';
import { useAuthStore } from '../../store/useAuthStore';
import './css/MyPage.css';

export const MyPage = () => {
  const [member, setMember] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const [nicknameChecked, setNicknameChecked] = useState(false);
  const [emailVerified, setEmailVerified] = useState(false);
  const [newPassword, setNewPassword] = useState('');
  const [pwMatchMessage, setPwMatchMessage] = useState('');
  const [memberData, setMemberData] = useState({
    nickname: '',
    address: '',
    detailAddress: '',
    postcode: ''
  });

  const { token } = useAuthStore();

  useEffect(() => {
    const fetchMember = async () => {
      try {
        const res = await caxios.get('http://localhost/api/member/me', {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });
        setMember(res.data);
        setMemberData({
          nickname: res.data.nickname,
          address: res.data.address,
          detailAddress: res.data.detailAddress,
          postcode: res.data.postcode
        });
      } catch (err) {
        alert(' .');
      }
    };
    fetchMember();
  }, [token]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setMemberData((prev) => ({ ...prev, [name]: value }));
  };

  const checkNickname = async () => {
    try {
      const res = await caxios.post('/api/member/checkNickName/${memberData.nickname}');
      if (res.data.available) {
        alert('  .');
        setNicknameChecked(true);
      } else {
        alert('   .');
        setNicknameChecked(false);
      }
    } catch (err) {
      alert('    ');
    }
  };

  const openAddressPopup = () => {
    new window.daum.Postcode({
      oncomplete: function(data) {
        setMemberData(prev => ({
          ...prev,
          address: data.address,
          postcode: data.zonecode
        }));
      }
    }).open();
  };

  const requestEmailVerification = async () => {
    try {
      await caxios.post('/api/member/request-email-auth');
      alert('  .');
    } catch (err) {
      alert('  ');
    }
  };

  const changePassword = async () => {
    try {
      await caxios.post('/api/member/change-password', { newPassword });
      alert(' .');
      setEmailVerified(false);
      setNewPassword('');
    } catch (err) {
      alert('  ');
    }
  };

  const handleSave = async () => {
    if (!nicknameChecked) return alert('   .');
    try {
      await caxios.put('/api/member/me', memberData, {
        headers: { Authorization: `Bearer ${token}` }
      });
      alert('  .');
      setEditMode(false);
    } catch (err) {
      alert('  ');
    }
  };

  if (!member) return <div>   ...</div>;

  return (
    <div className="mypage-container">
      <h2></h2>
      <div className="info-box">
        <p><strong>:</strong> {member.loginId}</p>
        <p><strong>:</strong> {member.email}</p>
        <p><strong>:</strong> {member.role}</p>
        <p><strong>:</strong> {member.regDate}</p>

        {editMode ? (
          <>
            <p>
              <strong>:</strong>
              <input name="nickname" value={memberData.nickname} onChange={handleChange} />
              <button onClick={checkNickname}> </button>
            </p>
            <p>
              <strong>:</strong>
              <input name="postcode" value={memberData.postcode} readOnly />
              <button onClick={openAddressPopup}> </button>
            </p>
            <p>
              <strong>:</strong>
              <input name="address" value={memberData.address} onChange={handleChange} />
            </p>
            <p>
              <strong>:</strong>
              <input name="detailAddress" value={memberData.detailAddress} onChange={handleChange} />
            </p>
            <p>
              <strong> :</strong>
              <input type='password'></input>
                <div className="text-sm" style={{ color: pwMatchMessage.includes('') ? 'red' : 'green' }}>
          {pwMatchMessage}
        </div>
              <button onClick={requestEmailVerification}>  </button>
              {emailVerified && (
                <input
                  type="password"
                  placeholder=" "
                  value={newPassword}
                  onChange={(e) => setNewPassword(e.target.value)}
                />
              )}
              {emailVerified && <button onClick={changePassword}> </button>}
            </p>
            <button onClick={handleSave}></button>
            <button onClick={() => setEditMode(false)}></button>
          </>
        ) : (
          <>
            <p><strong>:</strong> {member.nickname}</p>
            <p><strong>:</strong> {member.postcode}</p>
            <p><strong>:</strong> {member.address}</p>
            <p><strong>:</strong> {member.detailAddress}</p>
            <button onClick={() => setEditMode(true)}></button>
          </>
        )}
      </div>
    </div>
  );
};

export default MyPage;
